import { mapState } from "vuex";
import uploadFile from '../../../core/upload.js';

export default {
    data() {
        return {
            dialog: false,
            level: [
                {value: 1, label:'一级'},{value: 2, label:'二级'},{value: 3, label:'三级'}
            ],
            iphone_x: false,
            showParams: false,
            parents: null,
            parents_two: null,
            mode: '',
            params: [],
            pickList: [],
            linkType: [
                {value: 'base', name: '基础'},
                {value: 'order', name: '订单'},
                {value: 'marketing', name: '营销'},
                {value: 'plugin', name: '插件'},
                {value: 'diy', name: '微页面'},
            ],
            chooseType: 'base',
            title: '',
            linkList: [],
            list: [],
            mch_id: null,
            id: 0,
            type: '',
            newIndex: 0,
            chooseIndex: [0],
            catItem: {
                level: 1,
                name: '',
                sort: 100,
                pic_url: '',
                big_pic_url: '',
                advert_pic: '',
                status: 1,
                advert_url:'',
                advert_open_type: '',
                advert_params: [],
                is_show: 1,
                parent_id: '',
            },
            color: ''
        }
    },
    computed: {
        ...mapState({
            userInfo: state => state.user.info,
            adminImg: state => state.mallConfig.__wxapp_img.app_admin
        })
    },
    methods: {
        paramsConfirm() {
            for(let item of this.params) {
                if(item.is_required && !item.value) {
                    uni.showToast({
                        title: item.desc,
                        icon: 'none',
                        duration: 1000
                    });
                    return false;
                }
            }
            this.dialog = false;
            this.showParams = false;
            this.catItem.advert_url = this.pickList[this.newIndex].value;
            this.catItem.advert_open_type = this.pickList[this.newIndex].open_type;
            this.catItem.advert_params = this.params;
        },
        toggleType(item) {
            this.chooseType = item;
            this.pickList = [];
            this.newIndex = 0;
            this.chooseIndex[0] = 0;
            setTimeout(()=>{
                this.pickList = this.linkList[item];
            },200)
        },
        getList(type) {
            this.pickList = [];
            this.mode = type;
            this.newIndex = 0;
            this.chooseIndex[0] = 0;
            if(type == 'one') {
                let list = [];
                for(let item of this.list) {
                    if(this.catItem.level == 3) {
                        if(item.children) {
                            list.push(item)
                        }
                    }else {
                        list.push(item)
                    }
                }
                for(let index in list) {
                    if(this.parents && this.parents.value == list[index].value) {
                        this.newIndex = +index;
                        this.chooseIndex[0] = +index;
                    }
                    this.pickList.push(list[index])
                }
                if(this.pickList.length == 0) {
                    uni.showToast({
                        title: '暂无可用的一级分类',
                        icon: 'none',
                        duration: 1000
                    });
                    this.catItem.level--;
                    return false;
                }
                this.$nextTick(() => {
                    this.dialog = true;
                })
            }
            if(type == 'two') {
                if(!this.parents) {
                    uni.showToast({
                        title: '请选择归属的一级分类',
                        icon: 'none',
                        duration: 1000
                    });
                    return false;
                }
                if(this.parents.children) {
                    for(let index in this.parents.children) {
                        if(this.parents_two && this.parents_two.value == this.parents.children[index].value) {
                            this.newIndex = +index;
                            this.chooseIndex[0] = +index;
                        }
                        this.pickList.push(this.parents.children[index])
                    }
                    if(this.pickList.length == 0) {
                        uni.showToast({
                            title: '暂无二级分类',
                            icon: 'none',
                            duration: 1000
                        });
                        this.catItem.level--;
                        return false;
                    }
                    this.$nextTick(() => {
                        this.dialog = true;
                    })
                }
            }
            if(type == 'ad') {
                this.pickList = this.linkList[this.chooseType];
                for(let index in this.linkList[this.chooseType]) {
                    if(this.catItem.advert_url && this.catItem.advert_url == this.linkList[this.chooseType][index].value) {
                        this.newIndex = +index;
                        this.chooseIndex[0] = +index;
                    }
                }
                this.$nextTick(() => {
                    this.dialog = true;
                })
            }
        },
        submitChoose() {
            if(this.mode == 'one') {
                this.dialog = false;
                if(this.parents && this.parents.value != this.pickList[this.newIndex].value) {
                    this.parents_two = null;
                }
                this.parents = this.pickList[this.newIndex];
            }else if(this.mode == 'two') {
                this.dialog = false;
                this.parents_two = this.pickList[this.newIndex];
            }else if(this.mode == 'ad') {
                if(this.pickList[this.newIndex].params) {
                    this.params = this.pickList[this.newIndex].params;
                    this.title = this.pickList[this.newIndex].name;
                    this.showParams = true;
                }else {
                    this.dialog = false;
                    this.catItem.advert_url = this.pickList[this.newIndex].value;
                    this.catItem.advert_open_type = this.pickList[this.newIndex].open_type;
                    this.catItem.advert_params = [];
                }
            }
        },
        bindChange(e) {
            this.newIndex = +e.detail.value[0]
        },
        getCat() {
            let that = this;
            let url = this.mch_id == 0 ? that.$api.app_admin.cat : that.type == 'mall' ? that.$api.mch.cat : that.$api.mch.mch_cat;
            let para;
            that.$request({
                url: url,
                data: {
                    mch_id: that.mch_id
                }
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    that.list = response.data.list;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
        getPickLine() {
            let that = this;
            that.$request({
                url: that.$api.app_admin.pick_link,
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    that.linkList = response.data.list;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
        add(name) {
            let that = this;
            this.menu = false;

            uni.chooseImage({
                count: 1,
                sizeType: ['original', 'compressed'],
                success(res) {
                    // tempFilePath可以作为img标签的src属性显示图片
                    const tempFilePaths = res.tempFilePaths;
                    uni.showLoading({
                        mask: true,
                        title: '上传中...'
                    });
                    tempFilePaths.forEach(function (row, index) {
                        let para = {
                            pic_url: '',
                            type: 'img',
                            id: 0
                        };
                        let fileName = '';



                        uploadFile({
                            url: that.$api.upload.file,
                            filePath: row,
                            fileType: 'image',
                            mch_id: 0,
                            fileName: fileName
                        }).then(res => {
                            uni.hideLoading();
                            let imgInfo = undefined;


                            imgInfo = JSON.parse(res.data);






                            if (imgInfo.code == 0) {
                                if(name == 'pic_url') {
                                    that.catItem.pic_url = imgInfo.data.url
                                }else if(name == 'big_pic_url') {
                                    that.catItem.big_pic_url = imgInfo.data.url
                                }else if(name == 'advert_pic') {
                                    that.catItem.advert_pic = imgInfo.data.url
                                }
                            }
                        });
                    })
                }
            })































































        },
        typeChange(e) {
            if(this.id > 0) return false;
            this.catItem.level = e.target.value;
            if(this.catItem.level == 3 && (this.parents &&!this.parents.children)) {
                this.parents = null;
            }
        },
        save() {
            let that = this;
            let url = this.mch_id == 0 ? that.$api.app_admin.cat_edit : that.type == 'mall' ? that.$api.mch.cat : that.$api.mch.mch_cat_edit;
            that.catItem.status = that.catItem.status.toString();
            if(that.catItem.level == 3) {
                if(that.parents_two && that.parents_two.value) {
                    that.catItem.parent_id = that.parents_two.value
                }else {
                    uni.showToast({
                        title: '请选择归属的二级分类',
                        icon: 'none',
                        duration: 1000
                    });
                    return false;
                }
            }else if(that.catItem.level == 2) {
                if(that.parents && that.parents.value) {
                    that.catItem.parent_id = that.parents.value
                }else {
                    uni.showToast({
                        title: '请选择归属的一级分类',
                        icon: 'none',
                        duration: 1000
                    });
                    return false;
                }
            }
            if(!that.catItem.name) {
                uni.showToast({
                    title: '分类名称不能为空',
                    icon: 'none',
                    duration: 1000
                });
                return false;
            }
            uni.showLoading({
                mask: true,
                title: '提交中...'
            });
            if(that.mch_id > 0) {
                that.catItem.mch_id = that.mch_id;
            }
            that.catItem.is_show = +that.catItem.is_show;
            that.catItem.status = that.catItem.status.toString();
            that.$request({
                url: url,
                data: {
                    form: JSON.stringify(that.catItem),
                    mch_id: that.mch_id
                },
                method: 'post'
            }).then(response=>{
                that.$hideLoading();
                uni.hideLoading();
                if(response.code == 0) {
                    uni.showToast({
                        title: response.msg,
                        duration: 1000
                    });
                    setTimeout(()=>{
                        uni.navigateBack();
                    },1000)
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
                that.$hideLoading();
            });
        },
        getDetail() {
            let that = this;
            let url = this.mch_id == 0 ? that.$api.app_admin.cat_edit : that.type == 'mall' ? that.$api.mch.cat : that.$api.mch.mch_cat_edit;
            let para;
            that.$request({
                url: url,
                data: {
                    id: that.id,
                    mch_id: that.mch_id
                }
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    that.catItem = response.data.detail;
                    that.catItem.level = response.data.detail.parents.length + 1;
                    if(that.catItem.level > 1) {
                        for(let item of that.catItem.parents) {
                            if(item.parent_id == 0) {
                                that.parents = {
                                    label: item.name,
                                    value: item.id
                                };
                            }else {
                                that.parents_two = {
                                    label: item.name,
                                    value: item.id
                                };
                            }
                        }
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
    },

    onLoad(options) {
        let that = this;
        this.id = options.id ? options.id : 0;
        this.type = options.type ? options.type : '';
        this.mch_id = options.mch_id ? options.mch_id : 0;
        this.color = this.mch_id > 0 ? '#ff4544' : '#446dfd';
        uni.setNavigationBarTitle({
            title: this.id > 0 ? '编辑分类' : '新增分类'
        });
        if(this.mch_id == 0) that.getPickLine();
        if(this.id > 0) {
            that.$showLoading({
                type: 'global',
                text: '加载中...'
            });
            this.getDetail();
        }
        uni.getSystemInfo({
            success: function (res) {
                if(res.model.indexOf('iPhone X') > -1 || res.model.indexOf('iPhone 11') > -1 || res.model.indexOf('iPhone11') > -1 || res.model.indexOf('iPhone12') > -1 || res.model.indexOf('Unknown Device') > -1) {
                    that.iphone_x = true;
                }
            }
        })
        that.getCat();
    }
}
